import { useState } from 'react'
import { useSprings, animated, to as interpolate } from '@react-spring/web'
import { useDrag } from '@use-gesture/react'

import './Deck.css';

const cards = [
  'https://upload.wikimedia.org/wikipedia/commons/f/f5/RWS_Tarot_08_Strength.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/5/53/RWS_Tarot_16_Tower.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/9/9b/RWS_Tarot_07_Chariot.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/3/3a/TheLovers.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/thumb/8/88/RWS_Tarot_02_High_Priestess.jpg/690px-RWS_Tarot_02_High_Priestess.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/d/de/RWS_Tarot_01_Magician.jpg',
  'https://upload.wikimedia.org/wikipedia/commons/thumb/9/94/Robert_Downey_Jr_2014_Comic_Con_%28cropped%29.jpg/440px-Robert_Downey_Jr_2014_Comic_Con_%28cropped%29.jpg'
]

// These two are just helpers, they curate spring data, values that are later being interpolated into css
const to = (i) => ({
  x: 0,
  y: i * -4,
  scale: 1,
  rot: -5 + Math.random() * 10,
  delay: i * 100,
})
const from = (_i) => ({ x: 0, rot: 0, scale: 1.5, y: -1000 })
// This is being used down there in the view, it interpolates rotation and scale into a css transform
const trans = (r, s) =>
  `perspective(1500px) rotateX(30deg) rotateY(${r / 10}deg) rotateZ(${r}deg) scale(${s})`

function Deck() {
  const [reviewed] = useState(() => new Set()) // cards to the left
  const [marked] = useState(() => new Set()) // marked cards
  const [props, api] = useSprings(cards.length, i => ({
    ...to(i),
    from: from(i),
  })) // Create a bunch of springs using the helpers above
  // Create a gesture, we're interested in down-state, delta (current-pos - click-pos), direction and velocity
  const bind = useDrag(({ args: [index], active, movement: [mx, my], direction: [xDir, yDir], velocity: [vx, vy] }) => {
    // console.log({vx, vy});
    // console.log({mx, my});
    // console.log({xDir, yDir});
    const leftSwipe = (vx > 0.2 && xDir === -1);
    const downSwipe = (vy > 0.2 && yDir === -1);
    if (!active) {
        if (leftSwipe) {
            reviewed.add(index);
        } else if (downSwipe) {
            marked.add(index);
        }
    } else {
      if (mx < -100) {
        reviewed.add(index)
      } else if (my > 300) {
        marked.add(index);
      }
    }

    api.start(i => {
      if (index !== i) return // We're only interested in changing spring-data for the current spring
      const isReviewed = reviewed.has(index);
      const isMarked = marked.has(index);
      let x, y;
      if (-mx > my) {
        x = isReviewed ? -200 : active ? mx : 0;
        y = active ? my : 0;
      } else {
        y = isMarked ? 400 : active ? my : 0;
        x = active ? mx : 0;
      }
      console.log({x, y});
    //   const rot = mx / 100 + (isReviewed ? xDir * 10 * vx : 0) // How much the card tilts, flicking it harder makes it rotate faster
      const rot = 0;
      const scale = active ? 1.1 : 1 // Active cards lift up a bit
      return {
        x,
        y,
        rot,
        scale,
        delay: undefined,
        config: { friction: 50, tension: active ? 800 : isReviewed ? 200 : 500 },
      }
    })
    if (!active && (reviewed.size + marked.size) === cards.length)
      setTimeout(() => {
        reviewed.clear();
        marked.clear();
        api.start(i => to(i))
      }, 600)
  })
  // Now we're just mapping the animated values to our view, that's it. Btw, this component only renders once. :-)
  return (
    <div className='container'>
      {props.map(({ x, y, rot, scale }, i) => (
        <animated.div className='deck' key={i} style={{ x, y }}>
          {/* This is the card itself, we're binding our gesture to it (and inject its index so we know which is which) */}
          <animated.div
            {...bind(i)}
            style={{
              transform: interpolate([rot, scale], trans),
              backgroundImage: `url(${cards[i]})`,
            }}
          />
        </animated.div>
      ))}
    </div>
  )
}

export default Deck;
